#planetSelector {
  pointer-events: none;
  opacity: 0;
  z-index: 10;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
  height: 200px;
  display: flex;
  position: absolute;
  bottom: 0;
}

#planetCenter {
  justify-content: center;
  align-items: center;
  width: 140px;
  height: 130px;
  transition: opacity 2s;
  display: flex;
  transform: rotate(-90deg);
}

#planetCenter circle {
  stroke: #fff;
  stroke-width: 5px;
  fill: none;
  stroke-dasharray: 314;
  stroke-dashoffset: 314px;
  transition: stroke-dashoffset 2s, stroke-width .2s;
}

#planetCenter:hover circle {
  stroke-width: 7px;
}

.arrow {
  color: #fff;
  opacity: 0;
  pointer-events: none;
  background: none;
  border: none;
  font-size: 2rem;
  transition: transform .3s;
}

.arrow:hover {
  transform: scale(1.2);
}

.sweep-left {
  clip-path: inset(0 0 0 100%);
  animation: 1s forwards sweepInLeft;
}

.sweep-right {
  clip-path: inset(0 100% 0 0);
  animation: 1s forwards sweepInRight;
}

@keyframes sweepInLeft {
  0% {
    clip-path: inset(0 0 0 100%);
    opacity: 1;
  }

  100% {
    clip-path: inset(0);
    opacity: 1;
    pointer-events: auto;
  }
}

@keyframes sweepInRight {
  0% {
    clip-path: inset(0 100% 0 0);
    opacity: 1;
  }

  100% {
    clip-path: inset(0);
    opacity: 1;
    pointer-events: auto;
  }
}
